@use 'base/variables' as v;
@use 'base/mixins' as m;

.menu {
   position: fixed;
   top: 0;
   left: 0;
   bottom: 0;
   z-index: 2;
   display: flex;
   flex-direction: column;
   width: 80%;
   overflow: auto;
   background-color: darken(v.$azul, 30%);
   padding: 2rem;
   transform: translate3d(-100%, 0, 0);
   transition: transform .4s cubic-bezier(0.23, 1, 0.32, 1);
   @include m.lista;

   @include m.tablet {
      width: 60%;
   }

   @include m.desktop {
      position: static;
      width: 30rem;
      background-color: transparent;
      transform: none;
      padding: 0;
      box-shadow: none;
   }

   &__header {
      display: flex;
      justify-content: space-between;
      padding: 2rem;
      margin-bottom: 2rem;

      @include m.desktop {
         justify-content: center;
      }
   }

   &__logo {
      max-width: 10rem;
   }

   &__close {
      @include m.enlace;
      & {
         font-size: 3rem;
      }

      &:hover {
         color: darken(v.$blanco, 10%);
      }

      @include m.desktop {
         display: none;
      }
   }

   &__item {
      padding: 1.2rem 2rem;
      
      i {
         width: 1.8rem;
         text-align: center;
      }

      &--sub {
         margin-top: 1rem;
         i {
            width: auto;
         }
      }

      &:hover {
         cursor: pointer;
         background-color: lighten(v.$dark, 0%);
         transition: background-color .5s ease;
         color: v.$blanco;
      }
   }

   &__enlace {
      color: darken(v.$blanco, 40%);
      font-weight: v.$medium;
      white-space: nowrap;
      display: flex;
      flex-direction: row;
      align-items: center;
      gap: 1rem;
   }

   &__nombre {
      flex-grow: 1;
   }

   &__icono {
      &::after {
         content: "";
         width: .6rem;
         height: .6rem;
         border-bottom: 2px solid v.$blanco;
         border-right: 2px solid v.$blanco;
         rotate: -45grad;
         margin-right: .2rem;
      }
   }

   &__submenu {
      @include m.lista;
      padding-left: 2rem;
      font-size: 1.2rem;
      display: none;
   }

   &__activo {
      background-color: lighten(v.$dark, 0%);
   }

   &__activo &__enlace{
      color: v.$blanco;
   }

   &__activo &__submenu {
      display: block;
   }

   &__activo &__icono {
      &::after {
         rotate: 50grad;
      }
   }

   // &__redes {
   //    @include m.redes(v.$azul, 2rem);
   // }

}